    margin: 0px;
    padding: 0px;
    box-sizing: border-box;
}

:root {
    --color1: #f3f6fc;
    --color2: #70d1f1;
    --color3: #ea4335;
    --color4: #fbbc04;
    --color5: #34a853;
    --color6: #ffffff;
    --color7:darkcyan;
}

body {
    background-color: rgb(16, 130, 230);
    font-family: "Roboto Slab", serif;
}

h1 {
    font-family: system-ui;
}

.wrapper {
    height: 100vh;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: 10% 80% 10%;
}

nav {
    background: var(--color2);
    grid-area: 1 / 1 / 2 / 5;
}

aside {
    background: var(--color1);
    grid-area: 2 / 1 / 3 / 2;
}

main {
    background: rgb(255, 255, 255);
    grid-area: 2 / 2 / 3 / 5;
    overflow: auto;
}

footer {
    background: var(--color2);
    grid-area: 3 / 1 / 4 / 5;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.2rem;
}

span.color {
    display: block;
    width: 10px;
    height: 10px;
    border-radius: 50%;
}

aside,
.content {
    display: flex;
    flex-direction: column;
    gap: 15px;
    overflow: auto;
}

.heading {
    gap: 200px;
}

.todoItems {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(249px, 1fr));
    gap: 15px;
    overflow: auto;
    margin-top: 15px;
    overflow-wrap: anywhere;
}

nav ul {
    display: flex;
    list-style: none;
    justify-content: center;
    gap: 20px;
    align-items: center;
    height: 100%;
    font-size: 1.2rem;
}

dialog {
    inset: 0px;
    margin: auto;
    position: absolute;
    background: rgb(238, 238, 238);
    padding: 4px;
}

.highlight {
    background-color: var(--color6);
    padding: 2px;
}

.fa-close {
    color: var(--color3);
}

.fa-plus,
.fa-edit {
    color: var(--color5);
}

.fa-eye {
    color: var(--color2);
}

.content>div,
.todoItems>div {
    display: flex;
    align-items: baseline;
    gap: 10px;
    padding: 5px 20%;
}

.projectName {
    display: flex;
    align-items: baseline;
    gap: 5px;
}

.projectFeature {
    display: flex;
    margin-left: auto;
    gap: 10px;
}

span.priority {
    font-family: monospace;
    border: 2px solid;
    font-size: 0.79rem;
    padding: 1px 5px;
    font-weight: bold;
    border-radius: 50%;
}

.todoItems>div {
    padding: 5px 10px;
    flex-direction: column;
}

span {
    overflow: auto;
}

h2 {
    padding: 5px 0px 0px 5px;
    color: var(--color2);
}

button.closeProject,
button.todoDialogClose {
    display: block;
    margin-left: auto;
    background: var(--color3);
    border: 1px solid var(--color3);
}

form {
    padding: 10px;
}

form>div {
    display: flex;
    flex-direction: column;
    margin: 10px 0px;
    gap: 3px;
}

input[type="color"] {
    height: 52px;
    width: auto;
}

button.projectBtn,
button.todoBtn {
    margin: 10px 0px;
    padding: 5px 15px;
    background: var(--color2);
    border: 1px solid var(--color2);
    border-radius: 4px;
}

input,
button.projectBtn,
button.todoBtn,
button.closeProject,
button.todoDialogClose,
select {
    padding: 10px;
    font-size: 1.2rem;
}

.todoItems>div>div:nth-child(2) {
    display: flex;
    align-items: center;
    gap: 5px;
}

nav,footer {
    color: var(--color7);
}

